<template>
    <div id="lyric">
        <span class="title">播放列表</span>
        <div>
            <ul class="song-info-wrapper" ref="ul">
                <li v-for="(itema,index) in data" @click="change(index)" >{{itema.song}} - <span>{{itema.singer}}</span> </li><li v-for="item in [1,2,3,4,1,2,2,22,8]"> <span>{{item}}</span></li>
            </ul>
        </div>
    </div>
</template>

<script>
import vue from "vue";
export default {
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      show:''
    };
  },
  props: ["data","index"],
  methods:{
      change(index){
          this.$emit('list',index)
      }
    },
    watch:{
        index:function(){
            var self = this
            vue.nextTick(function(){
                var node = self.$refs.ul.childNodes
                for(var i = 0; i < node.length; i ++){
                    node[i].classList.remove('red')
                }
                self.$refs.ul.childNodes[self.index].classList.add('red')
            })
        }
    },
    created(){
        var self = this
        vue.nextTick(function(){
            // self.$refs.ul.childNodes[self.index].classList.add('red')
        })
    }
}

</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
#lyric 
    .title 
        font-size: 20px;
        position: relative;
        z-index: 99;
        width: 100%;
        height: 36px;
        line-height: 36px;
        text-align: center;
        display: inline-block;
        background-color: #e9e9e9;
        border-bottom: 1px solid rgba(200, 200, 200, 0.8);
    div 
        width: 100%;
        height: 33vh;
        position: absolute;
        bottom: 60px;
        border-radius: 10px 10px 10px 10px;
        overflow: auto;
        background-color: rgba(210, 210, 210, 0.8)
        .song-info-wrapper 
            text-align: left;
            padding: 0 20px;
            font-size: 22px;
            position: relative;
            padding-top: 30px;
            li 
                padding-left: 10px;
                height: 50px;
                line-height: 50px;
                border-bottom: 2px solid rgba(100, 100, 100, 0.5);
            .red 
                    color red
                span 
                    font-size: 18px;
                
            
        
    

</style>



