<template>
    <div class="lyric">
        <ul >
            <li v-for="item in arr">{{item | trimStr | back}}</li>
        </ul>
    </div>
</template>
<script>
const arr =  ['[00:00.62]   丑八怪 ',
           ' [00:04.03]演唱：薛之谦',
            '[00:05.24]作词：甘世佳',
           ' [00:06.26]作曲：李荣浩',
            '[00:07.18]',
           ' [00:08.19]专辑：丑八怪',
            '[00:20.26]如果世界漆黑 其实我很美',
           ' [00:23.88]在爱情里面进退 最多被消费',
            '[00:27.70]不管同样的是非',
           ' [00:29.69]又怎么不对 无所谓',
            '[00:35.69]如果像你一样 总有人谄媚',
            '[00:39.32]围绕着我的卑微 也许能消退',
            '[00:43.32]其实我并不在意 有很多机会',
           ' [00:46.69]像巨人一样的无畏',
           ' [00:49.51]放纵我心里的鬼',
            '[00:51.20]可是我不配',
           ' [00:54.46]丑八怪 能否别把灯打开',
           ' [01:02.21]我要的爱 出没在漆黑一片的舞台',
            '[01:09.46]丑八怪 在这暧昧的时代',
            '[01:17.58]我的存在 像意外',
           ' [01:24.48]',
            '[01:37.79]有人用一滴泪 会红颜祸水',
            '[01:41.35]有人丢掉称谓 什么也不会',
            '[01:45.29]只要你足够虚伪',
         '   [01:47.10]就不怕魔鬼 对不对',
            '[01:53.05]如果剧本写好 谁比谁高贵',
            '[01:56.74]我只能沉默以对 美丽本无罪',
            '[02:00.63]当欲望开始贪杯 有更多机会',
            '[02:04.07]像尘埃一样的无畏',
            '[02:06.89]化成灰谁认得谁管他配不配',
           ' [02:08.65]',
            '[02:11.96]丑八怪 能否别把灯打开',
            '[02:19.52]我要的爱 出没在漆黑一片的舞台',
            '[02:26.83]丑八怪 在这暧昧的时代',
            '[02:34.96]我的存在 不意外',
            '[02:42.33]',
           ' [03:01.96]丑八怪 其实见多就不怪',
           ' [03:09.96]放肆去high 用力踩',
           ' [03:14.15]那不堪一击的洁白',
            '[03:17.21]丑八怪 这是我们的时代',
           ' [03:25.48]我不存在 才意外',
            '[03:32.29]']
export default {
    data(){
        return {
            arr:arr,
            srr:['s2','s4','s6','s8','s10'],
            show:'',
            ssrr:[]
        }
    },
    props:['time'],
    filters:{
        trimStr:function(str){return str.replace(/(^\s*)|(\s*$)/g,"");},
        back(str){return str.slice(10)},
        before(str){return str.slice(1,9)},
        changtime(str){return parseInt(str.slice(0,2)) * 60 + parseInt(str.slice(-5))}

    }

}
</script>
<style scope>
    .lyric{
        overflow:auto;
        width:100%;
        height:220px;
    }
    .lyric ul li{
        text-align: center;
        height:35px;
        width:100%;
        line-height: 35px;
        color:#fff;
    }
    .s2{
        opacity: 0.2;
    }
    .s4{
        opacity: 0.4;
    }
    .s6{
        opacity: 0.6;
    }
    .s8{
        opacity: 0.8;
    }
    .s10{
        opacity: 1;
    }

</style>




