<template>
  <div id="app">
    <audio :src="info.audio" ref="audio"></audio>
    <div class="wrapper">
      <div class="song-image">
        <img :src="info.image" alt="">
      </div>      
      <div class="song-info">
        <div class="singer-name">{{info.singer}}</div>
        <div class="song-name">{{info.song}}</div>
        <div class="album">{{info.album}}</div>
      </div>
      <div class="pro">
        <span class="start" @touchstart="touchChange">{{curtime}}</span>
        <div class="pro-wrapper" ref="proWrapper"  @touchstart="touchChange">
          <div class="new-time" :style="{transform:'translateX(' + precent + '%' + ')'}"></div>          
          <div class="all-time"></div>
        </div>
        <span class="end">{{endtime}}</span>
      </div>
      
      <song-lyric v-show="!listClass.show && index === 0" ></song-lyric>
      <v-lyric v-show="listClass.show" :data="data" @list="listChange" :index="index" ></v-lyric>
      <div class="control">
        <audio :src="info.audio"></audio>
        <div class="item-wrapper" :class="likeClass" @click="getChange(likeClass,true)"></div>
        <div class="item-wrapper" :class="prevClass" @click="getChange(prevClass,false,'prev')"></div>
        <div class="item-wrapper" :class="puaseClass" @click="getChange(puaseClass,true,'play')"></div>
        <div class="item-wrapper" :class="nextClass" @click="getChange(nextClass,false,'next')"></div>
        <div class="item-wrapper" :class="listClass" @click="getChange(listClass,true)"></div>
      </div>
    </div>
    <div class="background">
      <img :src="info.image" alt="">
    </div>
  </div>
</template>
<script>
import axios from "axios";
import Vue from "vue";
import lyric from "@/components/lyric"
import song from "@/components/songlyric"

const dataObj = {
  index: 0,
  info: "",
  data: "",
  width: "",
  timer: "",
  run: 0,
  curt: 0,
  likeClass: { like: true, "like-solid": false },
  prevClass: { prev: true },
  puaseClass: { pause: false, play: true },
  nextClass: { next: true },
  listClass: { playlist: true, list: false , show:false}
};

export default {
  name: "App",
  data() {
    return dataObj;
  },
  components:{
    "v-lyric":lyric,
    "song-lyric":song
  },
  computed: {
    endtime() {
      let time =
        "0" +
        parseInt(this.info.duration / 60) +
        ":" +
        ("0" + this.info.duration % 60).slice(-2);
      return time;
    },
    precent() {
      return -(100 - this.run / parseInt(this.width) * 100);
    },
    speed() {
      return parseInt(this.width) / this.info.duration;
    },
    curtime() {
      let time = Math.round(
        this.info.duration * (this.run / parseInt(this.width))
      );
      return (
        ("0" + parseInt(time / 60)).slice(-2) +
        ":" +
        ("0" + time % 60).slice(-2)
      );
    }
  },
  watch:{
    run(){
      if (this.run <= 0) {
        this.run = 0;
      }
      if (this.run >= parseInt(this.width)) {
        this.run = parseInt(this.width)
        var self = this
        setTimeout(function(){
          self.goTo(1);
        },500)
        
      }
    }

  },
  methods: {
    listChange(index){
      this.index = index
      this.run = 0;
      clearInterval(this.timer);
      this.info = this.data[this.index];
      this.likeClass = { like: true, "like-solid": false };
      this.prevClass = { prev: true };
      this.puaseClass = { pause: false, play: true };
      this.nextClass = { next: true };
      
    },
    initClass() {
      this.likeClass = { like: true, "like-solid": false };
      this.prevClass = { prev: true };
      this.puaseClass = { pause: false, play: true };
      this.nextClass = { next: true };
      this.listClass = { playlist: true, list: false , show:false};
    },
    changeRun(type, self, e) {
      if (type === "timer") {
        self.timer = setInterval(function() {
          self.run += self.speed;
        }, 1000);
      } else if (type === "splid") {
        self.run = e.targetTouches[0].clientX - 60;
        if (self.run <= 0) {
          self.run = 0;
        }
        if (self.run >= parseInt(self.width)) {
          self.run = parseInt(self.width) - 1
        }   
      }
    },
    touchChange() {
      var self = this;
      document.getElementsByTagName("body")[0].ontouchmove = function(e) {
        clearInterval(self.timer);
        self.changeRun("splid", self, e);
        self.changeRun("timer", self, e);
        self.$refs.audio.currentTime = self.run / self.speed
        self.$refs.audio.play();
        self.puaseClass = { pause: true, play: false };
        document.getElementsByTagName("body")[0].ontouchend = function() {
          this.ontouchmove = null;
        };
      };
    },
    goTo(num) {
      this.index = (this.index + num) % 3;
      this.run = 0;
      clearInterval(this.timer);
      this.info = this.data[this.index];
      this.initClass();
    },
    goPlay() {
      var self = this;
      this.$refs.audio.play();
      this.changeRun("timer", self);
    },
    getChange(obj, bool, str) {
      if (bool) {
        if(bool === this.listClass){
          if(this.listClass.show === false){
            this.$set(this.listClass,'show',false)
          }else{
            this.$set(this.listClass,'show',true)
          }
        }
        for (var item in obj) {
          if (obj.hasOwnProperty(item)) {
            obj[item] = !obj[item];
          }
        }
      }
      if (str === "prev") {
        this.goTo(2);
      } else if (str === "next") {
        this.goTo(1);
      } else if (str === "play") {
        if (this.$refs.audio.paused) {
          this.goPlay();
        } else {
          this.$refs.audio.pause();
          clearInterval(this.timer);
        }
      }
    }
  },
  created() {
    let a = this;
    Vue.nextTick(function() {
      a.width = window.getComputedStyle(a.$refs.proWrapper, null).width;
    });

    axios.get("https://www.easy-mock.com/mock/5ae6ed54713ba523da43aca5/music/getMusicInfo").then(
      res => {
         console.log(res.data)
        this.data = res.data;
        this.info = this.data[this.index];
       
    });
  }
};
</script>
<style lang="stylus" ref="stylesheet/stylus">
#app {
  position: relative;
  overflow: hidden;

  .background {
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    filter: blur(35px);
    width: 100%;
    height: 100%;

    img {
      background-size: cover;
      height: 100%;
    }
  }

  .wrapper {
    position: relative;
    padding-top: 60px;
    width: 100%;
    height: 100vh;
    box-sizing: border-box;

    .song-image {
      margin: 0 auto 10px;
      position: relative;
      width: 50%;
      padding-top: 50%;

      img {
        position: absolute;
        top: 0;
        // right 0
        // bottom 0
        // left 0
        width: 100%;
        height: 100%;
      }
    }

    .song-info {
      width: 100%;
      text-align: center;

      .singer-name {
        font-size: 24px;
        line-height: 36px;
        margin-bottom: 8px;
        color: rgba(255, 255, 255, 0.86);
      }

      .song-name, .album {
        font-size: 12px;
        line-height: 16px;
        color: rgba(255, 255, 255, 0.6);
        margin-top: 6px;
      }
    }

    .pro {
      display: flex;

      .start, .end {
        width: 60px;
        height: 40px;
        line-height: 40px;
        color: #ffffff;
        text-align: center;
      }

      .pro-wrapper {
        overflow: hidden;
        box-sizing: content-box;
        flex: 1;
        position: relative;

        .all-time, .new-time {
          position: absolute;
          top: 20px;
          height: 1px;
          width: 100%;
        }

        .new-time {
          transform: translateX(0);
          background-color: rgb(255, 255, 255);

          &::after {
            content: '';
            display: inline-block;
            position: absolute;
            top: -3px;
            right: -3px;
            width: 6px;
            height: 6px;
            border-radius: 50%;
            background-color: #ffffff;
          }
        }

        .all-time {
          background-color: rgba(255, 255, 255, 0.5);
        }
      }
    }

    .control {
      position: absolute;
      width: 100%;
      height: 60px;
      bottom: 0;
      background-color: rgba(0, 0, 0, 0.2);
      display: flex;

      .item-wrapper {
        flex: auto;
        background-size: 20px 20px;
        background-position: 50%;
        background-repeat: no-repeat;
      }

      .like {
        background-image: url('./assets/image/icon-like.png');
      }

      .like-solid {
        background-image: url('./assets/image/icon-like-solid.png');
      }

      .next {
        background-image: url('./assets/image/icon-next.png');
      }

      .pause {
        background-image: url('./assets/image/icon-pause.png');
      }

      .play {
        background-image: url('./assets/image/icon-play.png');
      }

      .prev {
        background-image: url('./assets/image/icon-prev.png');
      }

      .close {
        background-image: url('./assets/image/icon-close.png');
      }

      .list {
        background-image: url('./assets/image/icon-list.png');
      }

      .playlist {
        background-image: url('./assets/image/icon-playlist.png');
      }
    }
  }
}
</style>



